button.phl-handle {
  position: relative;
  height: 12px;
  width: 16px;
  margin-right: 8px;
  background: linear-gradient(to right, rgb(208, 212, 218), rgb(208, 212, 218));
  background-size: 100% 2px;
  background-position: center;
  background-repeat: no-repeat;
}

button.phl-handle::before,
button.phl-handle::after {
  content: '';
  position: absolute;
  height: 2px;
  width: 100%;
  left: 0;
  background: rgb(208, 212, 218);
}

button.phl-handle::before {
  top: 0;
}
button.phl-handle::after {
  bottom: 0;
}

.phl-expand,
.phl-collapse,
.phl-extract {
  position: relative;
  height: 20px;
  width: 20px;
  line-height: 12px;
  color: #a7a7a7;
  border-radius: 4px;
}

.phl-expand::after,
.phl-collapse::after {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  height: 8px;
  width: 8px;
  border: 1px solid #a7a7a7;
}

.phl-expand::after {
  border: 2px solid;
  transform: translate(-50%, -75%) rotate(45deg);
  border-left: none;
  border-top: none;
}
.phl-collapse::after {
  border: 2px solid;
  transform: translate(-50%, -25%) rotate(45deg);
  border-right: none;
  border-bottom: none;
}

